import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getRequests } from "../../../actions/requestAction";
import axios from "axios";
import CardAnalytics from "../../../components/CardAnalytics";

export default function TopCards() {
  const dispatch = useDispatch();
  const requests = useSelector((state) => state.requests);
  const [totalorders, setTotalOrders] = useState(0);
  const [totalrestaurants, setTotalRestaurants] = useState(0);
  const [totalusers, setTotalUsers] = useState(0);
  const getData = async () => {
    const orders = await axios.get("/api/calculations/orders");
    const { totalorders } = orders.data;
    setTotalOrders(totalorders);
    const users = await axios.get("/api/users");
    const { data } = users;
    setTotalUsers(data.length);
    const restaurants = await axios.get("/api/calculations/restaurants");
    const { totalrestaurants } = restaurants.data;
    setTotalRestaurants(totalrestaurants);
  };
  useEffect(() => {
    dispatch(getRequests());
  }, [dispatch]);
  useEffect(() => {
    let componentmounted = true;
    if (componentmounted) {
      getData();
    }
    return () => {
      componentmounted = false;
    };
  }, [totalorders, totalrestaurants, totalusers]);

  return (
    <div className="row">
      <CardAnalytics
        title="Requests"
        description="Total requests from chef"
        value={requests && Object.keys(requests).length}
        status="text-secondary"
      />
      <CardAnalytics
        title="Users"
        description="Total users"
        value={totalusers}
        status="text-secondary"
      />
      <CardAnalytics
        title="Orders"
        description="Total orders received"
        value={totalorders}
        status="text-secondary"
      />
      <CardAnalytics
        title="Restaurants"
        description="Total onboarded Restaurants"
        value={totalrestaurants}
        status="text-secondary"
      />
    </div>
  );
}
