import React, { useState } from 'react'

export default function CustomRadio({ values, handler }) {
    const [status, setStatus] = useState(values)
    const onRadioSelected = (e) => {
        const { name, value } = e.target
        handler(name, value)
    }
    return (
        status.map((data, index) => (
            <div className="form-check form-check-inline" key={index}>
                <input
                    className="form-check-input"
                    type="radio"
                    id={data}
                    name="status"
                    defaultChecked={data === 'Unapproved'}
                    value={data}
                    onClick={(e) => onRadioSelected(e)}
                />
                <label className="form-check-label" htmlFor={data}>
                    {data}
                </label>
            </div>
        ))
    )
}
