import React, { useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";

export default function Contacts() {
  const [contacts, setContacts] = useState([]);
  const fetchMails = async () => {
    const response = await axios.get("/api/contacts/");
    const { data } = response;
    setContacts(data);
  };
  useEffect(() => {
    let component = true;
    if (component) {
      fetchMails();
    }
    return () => {
      component = false;
    };
  }, []);
  return (
    <div className="row my-2">
      <div className="col-3">
        <div className="ibox">
          <div className="ibox-content ">
            <div className="file-manager">
              <a
                className="btn btn-block btn-primary compose-mail"
                href="/"
              >
                Compose Mail
              </a>
              <ul className="folder-list m-b-md mt-5" style={{ padding: 0 }}>
                <li>
                  <a href="/">
                    {" "}
                    <i className="fa fa-inbox " /> Inbox{" "}
                    <span className="label label-warning float-right">
                      16
                    </span>{" "}
                  </a>
                </li>
                <li>
                  <a href="/">
                    {" "}
                    <i className="fa fa-envelope-o" /> Send Mail
                  </a>
                </li>
                <li>
                  <a href="/">
                    {" "}
                    <i className="fa fa-file-text-o" /> Drafts{" "}
                    <span className="label label-danger float-right">2</span>
                  </a>
                </li>
                <li>
                  <a href="/">
                    {" "}
                    <i className="fa fa-trash-o" /> Trash
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div className="col-9 pl-0 ml-0">
        <div className="mail-box-header">
          <form
            // method="get"
            // action="index.html"
            className="float-right mail-search"
          >
            <div className="input-group">
              <input
                type="text"
                className="form-control form-control-sm"
                name="search"
                placeholder="Search email"
              />
              <div className="input-group-btn">
                <button type="submit" className="btn btn-sm btn-primary">
                  <i className="fa fa-search" />
                </button>
              </div>
            </div>
          </form>
          <h2>Inbox (16)</h2>
          <div className="mail-tools tooltip-demo m-t-md">
            <button
              className="btn btn-white btn-sm"
              data-toggle="tooltip"
              data-placement="left"
              title="Refresh inbox"
            >
              <i className="fa fa-refresh" />
            </button>
            <button
              className="btn btn-white btn-sm"
              data-toggle="tooltip"
              data-placement="top"
              title="Mark as read"
            >
              <i className="fa fa-eye" />{" "}
            </button>
            <button
              className="btn btn-white btn-sm"
              data-toggle="tooltip"
              data-placement="top"
              title="Move to trash"
            >
              <i className="fa fa-trash-o" />{" "}
            </button>
          </div>
        </div>
        <div className="mail-box">
          <table className="table table-hover table-mail">
            <tbody>
              {contacts.map((mail, index) => (
                <tr className="unread" key={index} >
                  <td className="mail-contact">
                    <small>{mail.sender_name}</small>
                  </td>
                  <td className="mail-subject">
                    <small>{mail.subject}</small>
                  </td>
                  <td className="text-right mail-date">
                    <small>
                      {moment(Date(mail.timestamp)).format('DD MMM YY hh:mm:ss A')}
                    </small>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
