import axios from "axios";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getPlans } from "../../../actions/planactions";
import Loading from "../../../utilities/Loading";

export default function ProfitMargin() {
  const [plan, setPlan] = useState([]);
  const [loading, setLoading] = useState(false);
  const { plans } = useSelector((state) => state.plans);
  const dispatch = useDispatch();

  useEffect(() => {
    let componentDidMount = true;
    dispatch(getPlans());
    if (componentDidMount) {
      setPlan(plans);
    }
    return () => {
      componentDidMount = false;
    };
  }, [plans,dispatch]);

  const onChangeText = async (target, id, key) => {
    const { name, value } = target;
    const plan = { profit_margin: value }
    await axios.put(`/api/plans/${id}`, plan)
    setPlan(plan =>
      plan.map(obj => {
        if (obj.code_name === name) {
          return { ...obj, profit_margin: value }
        }
        return obj
      })
    )
  };

  const done = async () => {
    setLoading(true);
    await dispatch(getPlans());
    alert("Plan successfully updated")
    setLoading(false);
  };

  return !loading ? (

    <div className="ibox-content">
      {
        Array.isArray(plan) && plan.map((item, key) => (
          <div className="row mt-1" key={key}>
            <div className="col-lg-12">
              <div className="form-group">
                <label>
                  {item.plan_name} <strong className="text-danger">*</strong>
                </label>
                <div className="input-group m-b">
                  <div className="input-group-prepend">
                    <span className="input-group-addon">$</span>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    name={`${item.code_name}`}
                    onChange={({ target }) => onChangeText(target, item._id, key)}
                    defaultValue={item.profit_margin}
                  />
                  <div className="input-group-append">
                    <span className="input-group-addon">.00</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))
      }
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2"
            onClick={done}
          >
            Update
          </button>
          <button type="button" className="btn btn-default float-right mr-2 ">
            Cancel
          </button>
        </div>
      </div>
    </div>

  ) : (
    <Loading />
  );
}
