import React, { useEffect, useState } from "react";
import StepWizard from "react-step-wizard";
import EditBankInfo from "./EditBankInfo";
import EditBasicInfo from "./EditBasicInfo";
import EditDocuments from "./EditDocuments";
import EditMeals from "./EditMeals";
import EditPlans from "./EditPlans";
import RestaurantTabNav from "../RestaurantTabNav";
import { useSelector, useDispatch } from "react-redux";

import { useParams } from "react-router";
import Loading from "../../../../utilities/Loading";
import { editBasicInfo, getRestaurant, updateRestaurant } from "../../../../actions/restaurantAction";
import axios from "axios";

export default function EditRestaurant(props) {
  const { restaurant } = useSelector(state => state)

  const [state, setState] = useState({});

  const [loaded, setLoaded] = useState(false);
  const [status, setStatus] = useState("")


  const onRadioSelected = ({ target }) => {
    const { value } = target
    setStatus(value)
    dispatch(updateRestaurant(id,{ status: value }))
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    setLoaded(false);
    const response = await axios.put('/api/newrest/' + id, restaurant)
    const { data } = response
    setLoaded(true);
  };

  const { id } = useParams();
  const dispatch = useDispatch()

  useEffect(() => {
    let componentMounted = true;
    if (componentMounted) {
      async function fetchData() {
        await dispatch(getRestaurant(id))
      }
      fetchData(id);
      setLoaded(true);
    }
    return () => {
      componentMounted = false;
    };
  }, [id]);

  useEffect(() => {
    let componentMount = true
    if (componentMount) {
      setState(restaurant)
    }
    return () => {
      componentMount = false
    }
  }, [loaded, id])

  useEffect(() => {
    let componentMount = true
    if (componentMount) {
      setStatus(restaurant.status)
    }
    return () => {
      componentMount = false
    }
  }, [restaurant])


  if (loaded) {
    return (
      <div className="wrapper wrapper-content">
        <div className="col-lg-12">
          <div className="ibox">
            <div className="ibox-title">
              <h5>Edit Restaurant</h5>
            </div>
            <div className="ibox-content">
              <form onSubmit={(e) => onSubmit(e)}>
                <div className="row justify-content-end mt-2">
                  <div className="form-check form-check-inline">
                    <input
                      className="form-check-input"
                      type="radio"
                      checked={status === "Unapproved" && true}
                      name="status"
                      value="Unapproved"
                      onChange={(e) => onRadioSelected(e)}
                    />
                    <label className="form-check-label" htmlFor="unapproved">
                      Unapproved
                    </label>
                  </div>
                  <div className="form-check form-check-inline">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="status"
                      checked={status === "Inactive" && true}
                      value="Inactive"
                      onChange={(e) => onRadioSelected(e)}
                    />
                    <label className="form-check-label" htmlFor="inactive">
                      Inactive
                    </label>
                  </div>
                  <div className="form-check form-check-inline">
                    <input
                      className="form-check-input"
                      type="radio"
                      checked={status === "Active" && true}
                      name="status"
                      value="Active"
                      onChange={(e) => onRadioSelected(e)}
                    />
                    <label className="form-check-label" htmlFor="active">
                      Active
                    </label>
                  </div>
                </div>
                <br />
                <StepWizard
                  isHashEnabled={false}
                  nav={<RestaurantTabNav {...props} />}
                >
                  <EditBasicInfo stepName={"Basic"} restaurant={state} />
                  <EditDocuments stepName={"Documents"} restaurant={state} />
                  <EditMeals stepName={"Meals"} restaurant={state} />
                  <EditPlans stepName={"Plans"} restaurant={state} />
                  <EditBankInfo stepName={"Bank Info"} restaurant={state} />
                </StepWizard>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return <Loading />;
  }
}
